import React, { Component } from 'react';


import Button from '../Button';
import { AndroidIcon } from './../Icon';
import './styles/About.scss'
import Title from '../Title/Title';

class About extends Component {
  render() {
    return (
      <div className="About-container">
        <section className='About' id='About'>
          <Title title='Sobre' />

          <p className='text'>
            O transporte pblico  certamente uma das principais solues para o caos da mobilidade urbana em nosso pas. No caso dos nibus,  soluo porque  um meio de transporte mais barato e ajuda desafogando as vias em nossas cidades. Em So Paulo por exemplo, um nibus transporta em mdia 48 passageiros por viagem, o que equivale a 40 carros ou 48 bicicletas  se levarmos em considerao que na cidade a mdia  de 1,2 pessoa por carro.
          </p>
          <ul className='text'>
            Vale destacar que:
            <li>1 nibus ocupa 50 metros quadrados</li>
            <li>48 bicicletas ocupam 92 metros quadrados</li>
            <li>40 carros ocupam 840 metros quadrados</li>
          </ul>
          <p className='text'>
            A superlotao, falta de linhas, constantes atrasos e pssima manuteno formam o cenrio real do
            transporte pblico no Brasil. No   toa que 70% das menes referentes a transporte pblico so negativas,
            evidenciando a falta de estrutura e o descontentamento da populao.
          </p>

          <img className='graphic' src="http://via.placeholder.com/200x200" alt="" />

          <p className='text'>
            Entre todas as reclamaes voltadas ao transporte pblico, a lotao dos coletivos  a mais presente, somando um
            total de 57,8% das menes. Logo em seguida, com 23,1%, aparece a demora em embarcar no coletivo,
            principalmente em nibus.
          </p>
          <p className='text'>
            O Busy app vem com uma proposta de melhorar o conforto do usurio dando a ele a possibilidade de escolha entre os nibus de acordo com seu nvel de lotao. 
          </p>



          <Button className="googlePlay">
            <AndroidIcon fill='#283033' />
            <p>Available on the <br /> <span>Google Play</span></p>
          </Button>
        </section>

        <img src="http://via.placeholder.com/300x500" alt="" />
      </div>
    )
  }
}

export default About;


